@use 'base/variables' as v;
@use 'base/mixins' as m;

.fa-solid {
    text-align: center;
    font-size: 2rem;
    @include m.tablet {
        font-size: 3rem;
    }
    @include m.desktopXL {
        font-size: 4rem;
    }
}

.links-news {
    text-align: center;
    .link {
        padding-bottom: 2vh;
    }
}

.main-links {
    margin-top: 10vh;
}

.links-research {
    @include m.tablet {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    }
}