@use "base/variables" as v;
@use "base/mixins" as m;

.main-students {
  margin-left: 0;
  margin-right: 0;
}

.students {
  width: 100%;
  margin-top: 5rem;
  .accordion-button:not(.collapsed) {
    color: unset;
    background-color: unset;
  }

  .accordion-item,
  .accordion-collapse {
    border: none;
  }

  .blueBg {
    background-color: v.$secundario;
    h4,
    p {
      color: v.$blanco;
    }
  }

  :focus {
    outline: 0;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }

  .accordion-button:not(.collapsed),
  .accordion-button:not(.collapsed):focus {
    box-shadow: rgb(0 0 0 / 0%) 0px 0px 0px;
  }

  .accordion-button {
    font-weight: bold;
    color: v.$secundario;
  }

  .accordion-header {
    margin-bottom: 2rem;
    margin-top: 2rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid v.$oscuro;
    margin-right: 4rem;
    margin-left: 4rem;
    @include m.telefono {
      margin-right: 5rem;
      margin-left: 5rem;
    }
    @include m.desktopXL {
      margin-right: 10rem;
      margin-left: 10rem;
    }
  }

  .accordion-body {
    padding: 0;
    @include m.telefono {
      margin-right: 0rem;
      margin-left: 0rem;
    }
    @include m.tablet {
      display: grid;
      grid-template-rows: auto;
    }
    @include m.desktopXL {
      margin-right: 0rem;
      margin-left: 0rem;
    }
  }
}

.accordion-student {
    padding: 1.5rem 0rem;
    padding-top: 5rem;
    @include m.telefono {
      padding-top: 5rem;
      padding-right: 5rem;
      padding-left: 5rem;
    }
    @include m.tablet {
        padding: 2rem auto;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 2rem;
    }
    @include m.desktop {
        padding: 4.5rem auto 0 auto;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        column-gap: 4rem;
    }
    @include m.desktopXL {
        padding-right: 10rem;
        padding-left: 10rem;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        column-gap: 4rem;
    }
    .imagen {
      width: 50%;
      @include m.tablet {
        width: 70%;
      }
    }
}

.student-description {
    margin-top: 2rem;
    margin-bottom: 3rem;
    @include m.tablet {
      margin-bottom: 4rem;
    }
    h4,
    p {
        text-align: center;
    }
    p {
        margin: 0;
        @include m.desktopXL {
          font-size: 1.4rem;
        }
    }
}

.no-display {
  display: none;
}