@use 'base/variables' as v;
@use 'base/mixins' as m;

.mb-1 {
    margin-bottom: 3rem !important;
    &:last-of-type {
        margin-bottom: 0rem !important;
    }
}

.card-body {
    padding: 2rem 2rem;
    text-align: start;
}

.card-title {
    text-align: start;
}

.card-text {
    a {
        display: block;
        font-size: 1.5rem;
        margin-bottom: 0.5rem;
    }
}