@use "base/variables" as v;
@use "base/mixins" as m;

.about {
  .imagen {
    width: 15rem;
    padding-bottom: 4rem;
  }
  padding: 2rem;
  @include m.tablet {
    display: flex;
    .imagen {
        flex: 0 0 33%;
        margin-left: 0;
    }
    .text {
        flex: 0 0 65%;
    }
  }
  @include m.desktopXL {
    .imagen {
        flex: 0 0 12%;
        margin-left: 0;
    }
    .text {
        flex: 0 0 85%;
    }
  }
}

.about-extra {
  padding: 0 2rem 2rem 2rem;
  p {
      @include m.desktopXL {
          text-align: center;
      }
  }
}

.links {
  address {
    margin: 0;
  }
  h3 {
    text-align: center;
    padding-bottom: 2rem;
  }
}

@include m.tablet {
  .links {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    .link {
      display: grid;
      grid-template-rows: 2fr 1fr;
    }
  }
}

.link {
  padding-bottom: 2.5rem;
}

.image-mail {
  padding-bottom: 2rem;
  width: 8rem;
}

.image-biomechatronics {
  width: 16rem;
}

.image-researchgate {
  width: 12rem;
}

.image-mail,
.image-biomechatronics,
.image-researchgate {
  margin-top: 2rem;
  padding-bottom: 2rem;
}
